import React, { useState, useCallback, useEffect } from 'react';
import classnames from 'classnames';
import Draggable from 'react-draggable';
import { Button } from 'antd';
import Modal from '../Modal';
import Img, { ImgProps } from '../Img';
import './styles.scss';

export interface PictureModalProps extends ImgProps {
  modalClassName?: string;
  /**
   * 默认放大倍数
   */
  defaultZoom?: number;
  /**
   * 默认旋转角度
   */
  defaultAngle?: number;
  visible?: boolean;
  onCancel?: () => void;
  /**
   * 默认宽度,一般不需要设置
   */
  width?: string | number;
}

const PictureModal: React.FC<PictureModalProps> = props => {
  const {
    defaultZoom = 1,
    defaultAngle = 0,
    visible,
    onCancel,
    modalClassName,
    className,
    width,
    style,
    ...rest
  } = props;
  const [zoom, setZoom] = useState(defaultZoom);
  const [angle, setAngle] = useState(defaultAngle);
  const plus = useCallback(() => {
    setZoom(prevZoom => prevZoom + 0.1);
  }, []);
  const minus = useCallback(() => {
    setZoom(prevZoom => {
      if (prevZoom > 0.2) return prevZoom - 0.1;
      return prevZoom;
    });
  }, []);
  const left = useCallback(() => {
    setAngle(prevAngle => prevAngle - 90);
  }, []);
  const right = useCallback(() => {
    setAngle(prevAngle => prevAngle + 90);
  }, []);
  const onScroll = useCallback(({ wheelDelta }) => {
    if (wheelDelta > 0) plus();
    if (wheelDelta < 0) minus();
  }, []);

  useEffect(() => {
    document.addEventListener('mousewheel', onScroll);
    return () => document.removeEventListener('mousewheel', onScroll);
  }, []);
  return (
    <Modal
      className={classnames(modalClassName, 'tea-picture-view-modal')}
      visible={visible}
      footer={null}
      onCancel={onCancel}
      width={width}
    >
      <div className="tea-picture-view-content">
        <Draggable>
          <div style={{ padding: 2 }}>
            <Img
              className={classnames(className, 'tea-picture-view-img')}
              style={{
                transform: `scale(${zoom}) rotate(${angle}deg)`,
                ...style,
              }}
              {...rest}
            />
          </div>
        </Draggable>
        <div className="tea-picture-view-btns">
          <Button
            size="large"
            ghost
            shape="circle"
            onClick={plus}
            icon="plus"
          />
          <Button
            size="large"
            ghost
            shape="circle"
            onClick={minus}
            icon="minus"
          />
          <Button
            size="large"
            ghost
            shape="circle"
            onClick={left}
            icon="undo"
          />
          <Button
            size="large"
            ghost
            shape="circle"
            onClick={right}
            icon="redo"
          />
        </div>
      </div>
    </Modal>
  );
};

PictureModal.defaultProps = {
  alt: '文件加载失败',
  defaultZoom: 1,
  defaultAngle: 0,
  width: '100vw',
  backupSrc:
    'data:image/svg+xml;base64,',
};
export default PictureModal;
